*{
    box-sizing: border-box;
}
form{
    width:30%
}
ul {
    margin:0;
    list-style: none;
    width:200px;
    padding:10px;
    border:1px solid black
}

h4, h3{
    margin:0;
    margin-bottom: 5px;
    text-align: center;
}

li{
    background-color: rgb(199, 199, 199);
    padding:5px;
    margin:10px 0;
    cursor: pointer;
    word-wrap: break-word;
}

li:hover{
    background-color: rgb(180, 180, 180);
}


p {
    margin:0
}

textarea{
    max-width: 300px;
}

.container{
    display: flex;
    justify-content: center;
    gap:50px
}

.note-description{
    width:40%;
    height:600px;
    background-color: rgb(231, 218, 200);
    margin-right:20px;
    padding:10px 20px
}

.activated{
    background-color: rgb(180, 180, 180);
}